import moment from "moment"; 

export default { 
  data(){  
    return{ 
      id:""
    }  
  },
  mounted: function () {
    $("#placeGroup-table").DataTable({ 
      paging: true,
      lengthChange: false,
      searching: true,
      ordering: false,
      info: true,
      autoWidth: true,
      responsive: true, 
      lengthMenu: [[10, 25, 50, -1], [10, 25, 50, "All"]],
      language: { 
        info: "เเสดง _START_ ถึง _END_ ของ _TOTAL_ รายการ",
        infoEmpty: "แสดง 0 ถึง 0 ของ 0 รายการ", 
        lengthMenu: "เเสดง _MENU_ รายการ",
        loadingRecords: "กำลังโหลด...",
        processing: "ประมวลผล...",
        search: "ค้นหา:", 
        emptyTable: "ไม่พบข้อมูล",
        paginate: {
          previous: "ก่อนหน้า",
          next: "ถัดไป",
          first: "เเรกสุด",
          last: "สุดท้าย",   
          infoEmpty: "ไม่พบข้อมูล", 
        }
      } 

    });
  },
  methods: {
    moment: function () {
      return moment();
    },
    alertDeletePlaceGroup(id){ 
      this.id = id; 
      $('#modal-delete-placeGroup').modal("show"); 
    },
    confirmDeletePlaceGroup(id){  
      $('#modal-delete-placeGroup').modal("hide");
      this.$emit('deleteData', id);
    }
  },
  props: {
    placeGroup: {
      type: Array,
      required: true,
    },
  },
  filters: {
    moment: function (date) {
      return moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
  } 
};
